import { Observable } from 'rxjs/Observable';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform } from 'ionic-angular';
import {YtProvider} from "../../../providers/yt/yt";

@IonicPage()
@Component({
  selector: 'page-playlist',
  templateUrl: 'playlist.html',
})
export class PlaylistPage {
  videos: Observable<any[]>;

  constructor(private navParams: NavParams, private ytProvider: YtProvider,
              private navCtrl: NavController,
               private plt: Platform) {
    let listId = this.navParams.get('id');
    this.videos = this.ytProvider.getListVideos(listId);
    console.log('videos' +
      ': ', this.videos);
  }

  openVideo(video) {



    this.navCtrl.push('VideoPlayer', {id: video.snippet.resourceId.videoId,desc:video.snippet.resourceId.description });
  }
}




